import React from 'react'
import Img from 'gatsby-image'
import { Container, Heading } from 'src/components/Base'
import { RichText } from 'prismic-reactjs'
import PropTypes from 'prop-types'

import styles from './styles.module.scss'

const Atf = ({ claim, imageLeft, imageRight, ...props }) => (
  <section className={styles.section} {...props}>
    <Container>
      <Heading as="h1" size="xl" className={styles.heading}>
        {RichText.asText(claim.raw)}
      </Heading>
    </Container>
    <div className={styles.gridContainer}>
      <Img
        className={styles.imgLeft}
        alt={imageLeft.alt}
        fluid={imageLeft.fluid}
      />
      <Img
        className={styles.imgRight}
        alt={imageRight.alt}
        fluid={imageRight.fluid}
      />
    </div>
  </section>
)

Atf.propTypes = {
  claim: PropTypes.shape({
    raw: PropTypes.array,
    text: PropTypes.string,
    html: PropTypes.string,
  }).isRequired,
  imageLeft: PropTypes.shape({
    fluid: PropTypes.object,
    alt: PropTypes.string,
  }).isRequired,
  imageRight: PropTypes.shape({
    fluid: PropTypes.object,
    alt: PropTypes.string,
  }).isRequired,
}

export default Atf
